
  .productContainer {
    width: 200px;
    margin: 0 10px;
  }
  .productImgContainer {
    width: 150px;
    height: 150px;
    overflow: hidden;
    margin: 10px auto;
    text-align: center;
  }
  .productContainer img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
  }
  .productInfo {
    text-align: center;
    font-size: 12px;
  }
  .productPrice {
    font-size: 14px;
    font-weight: bold;
    margin: 5px 0;
  }
  